import React from "react"

class Canvas extends React.Component {
    componentDidMount() {
        const canvas = this.refs.canvas
        const context = canvas.getContext("2d")
        const image = this.refs.canvasImg
        
        image.onload = () => {
            context.drawImage(image, 0, 0, canvas.width, canvas.height)
        }
    }

    handleDownload = (event) => {
        const canvas = this.refs.canvas
        const image = canvas.toDataURL()

        const tempLink = document.createElement("a")
        tempLink.download = "Generated Meme.png"
        tempLink.href = image

        document.body.appendChild(tempLink)
        tempLink.click()
        document.body.removeChild(tempLink)

        const altDownload = this.refs.altDownload
        altDownload.style.display = "block"
    }

    handleGen = (event) => {
        const canvas = this.refs.canvas
        const context = canvas.getContext("2d")
        const image = this.refs.canvasImg

        context.font = "2em Impact"
        context.textAlign = "center"
        context.strokeStyle = "black"
        context.lineWidth = 1.5
        context.fillStyle = "white"

        context.clearRect(0, 0, canvas.width, canvas.height)
        context.drawImage(image, 0, 0, canvas.width, canvas.height)

        context.fillText(this.props.topText.toUpperCase(), canvas.width / 2, 60)
        context.strokeText(this.props.topText.toUpperCase(), canvas.width / 2, 60)

        context.fillText(this.props.bottomText.toUpperCase(), canvas.width / 2, canvas.height - 35)
        context.strokeText(this.props.bottomText.toUpperCase(), canvas.width / 2, canvas.height - 35)

        if(this.props.randomImg || this.props.uploadedImg) {
            const download = this.refs.downloadBtn
            download.style.display = "block"
        }
    }

    render() {
        return(
            <div ref="generatedMeme" className="meme">
                <div className="text-fields">
                    <input
                        type="text"
                        name="topText"
                        placeholder="Enter Top Text"
                        value={this.props.topText}
                        onChange={this.props.eventHandler.handleChange}
                    />
                    <input
                        type="text"
                        name="bottomText"
                        placeholder="Enter Bottom Text"
                        value={this.props.bottomText}
                        onChange={this.props.eventHandler.handleChange}
                    />
                    <button onClick={this.handleGen}>Generate</button>
                </div>
                <canvas ref="canvas" width={600} height={400}></canvas>
                <img
                    style={{display:"none"}}
                    ref="canvasImg"
                    src={
                        this.props.modeSelector === "user" ?
                        this.props.uploadedImg :
                        this.props.randomImg
                    }
                    alt=""
                />
                <button
                    style={{display:"none"}}
                    className="download"
                    ref="downloadBtn"
                    onClick={this.handleDownload}
                >Download</button>
                <span style={{display:"none", marginTop:"10px"}} ref="altDownload">
                    Not downloading? Right click on the image and select <b>Save Image as...</b>
                </span>
            </div>
        )
    }
}

export default Canvas